<template>
  <main-card-container v-if="loading">
      <div slot="content">
        Chargement...
      </div>
    </main-card-container>
  <main-card-container v-else :title="!isNew ? 'Édition du vote' : 'Nouveau vote'">
    <form class="col-12" slot="content" @submit="openVote">
      <!-- infos générales -->
      <div class="mb-5 col-6 mx-auto">
        <h4>Informations générales</h4>
        <div class="form-group">
          <label for="title">Titre</label>
          <input
            id="title"
            type="text"
            class="form-control"
            name="title"
            aria-describedby="emailHelp"
            placeholder="Titre de votre vote"
            required
            v-model="title"
          />
        </div>
        <div class="form-group">
          <label for="question">Question</label>
          <textarea
            id="question"
            type="text"
            class="form-control"
            name="question"
            placeholder="Question non ambiguë"
            required
            v-model="description"
          />
        </div>
      </div>

      <!-- definition des choix -->
      <div class="mb-5 col-6 mx-auto" v-if="choices.length > 0">
        <h4>Propositions</h4>
        <div id="propositions">
          <MultiInput v-on:childToParent="updateChoices" :initialValues="choices" :notEnough="notEnoughChoices" />
        </div>
      </div>

      <!-- délai de suppression -->
      <div class="mb-5 col-6 mx-auto" v-if="isNew && !userIsLogged">
        <h4>Suppression</h4>
        <label for>Après</label>
        <div class="custom-control custom-radio">
          <input
            type="radio"
            id="one"
            name="limit"
            class="custom-control-input"
            :value="1"
            v-model="pickedDelay"
          />
          <label class="custom-control-label" for="one">1 jour</label>
        </div>
        <div class="custom-control custom-radio">
          <input
            type="radio"
            id="two"
            name="limit"
            class="custom-control-input"
            :value="2"
            v-model="pickedDelay"
          />
          <label class="custom-control-label" for="two">2 jours</label>
        </div>
        <div class="custom-control custom-radio">
          <input
            type="radio"
            id="seven"
            name="limit"
            class="custom-control-input"
            :value="7"
            v-model="pickedDelay"
          />
          <label class="custom-control-label" for="seven">7 jours</label>
        </div>
      </div>

      <div class="d-flex">
        <button
          v-if="!isNew"
          id="saveVote"
          type="button"
          class="btn btn-outline-success mr-1"
          @click="saveVote"
        >Enregistrer</button>
        <button type="button" class="btn btn-light ml-1" @click="cancelModifications">Annuler</button>
        <button id="publishVote" type="submit" class="btn btn-primary ml-auto">Ouvrir le vote</button>
      </div>
    </form>
  </main-card-container>
</template>

<script>
import MultiInput from "@/components/MultiInput";
import ApiCalls from "@/mixins/api-calls.vue";
import MainCardContainer from '../components/MainCardContainer.vue';

export default {
  name: "EditVote",
  components: {
    MultiInput,
    MainCardContainer
  },
  mixins: [ApiCalls],
  data: function() {
    return {
      API_URL: process.env.VUE_APP_API_URL,
      isNew: this.$route.name !== "EditVote",
      userIsLogged: !!localStorage.getItem('currentUser'),
      currentUser: JSON.parse(localStorage.getItem('currentUser')),
      token: localStorage.getItem('user-token'), 
      vote: {},
      title: "",
      user_id: null,
      description: "",
      status: 1,
      openDate: null,
      nbChoices: 0,
      deleteDate: null,
      pickedDelay: 1,
      choices: [],
      notEnoughChoices: false,
      loading: true,
    };
  },
  created() {
    if (!this.isNew) {
      this.findRecord('votes', { filter: {_id: this.$route.params.vote_id}})
        .then(response => {
          this.vote = response.data.entries[0];
          if (this.vote.nbChoices > 0) {
            this.findRecord('choices', { filter: { vote_id: this.$route.params.vote_id }})
              .then(response => {
                this.choices = response.data.entries.map(choice => {
                  return { val: choice.name };
                });
              });
          } else this.choices = [{ val: "" }, { val: "" }];
          this.title = this.vote.title;
          this.user_id = this.vote.user_id;
          this.description = this.vote.description;
          this.status = this.vote.status;
          this.openDate = this.vote.openDate;
          this.nbChoices = this.vote.nbChoices;
          this.deleteDate = this.vote.deleteDate || null;
          this.loading = false
        })
        .catch(e => {
          console.error(e);
        });
    } else this.loading = false
  },
  methods: {
    updateChoices(val) {
      this.choices = val;
    },
    addDays(date, days) {
      let result = new Date(date);
      result.setDate(result.getDate() + days);
      return result;
    },
    cancelModifications() {
      if (confirm("Êtes-vous sûr de vouloir perdre vos modifications ?"))
        this.$router.push('/account/' + this.currentUser._id).catch(err => {
          throw new Error("Problem handling something : " + err)
        })
    },
    getDatas(infos) {
      const data = {
        _id: this.vote._id,
        title: this.title,
        user_id: this.user_id || process.env.VUE_APP_ANONYM,
        description: this.description,
        status: infos.status,
        openDate: this.openDate || new Date(),
        nbChoices: this.choices.reduce((acc, c) => acc + (c.val ? 1 : 0), 0),
        nbBulletins: 0,
        deleteDate:
          this.isNew && !this.userIsLogged
            ? this.addDays(this.openDate, this.pickedDelay)
            : null
      };
      return data;
    },
    saveVote() {
      let datas = this.getDatas({ status: this.status });
      this.saveModifications(datas);
    },
    openVote(e) {
      e.preventDefault();
      if(!confirm('Êtes-vous sûre de vouloir ouvrir le vote ? Vous ne pourrez plus modifier après.')) return;
      
      const datas = this.getDatas({ status: 1});
      if (datas.nbChoices < 2) {
        this.notEnoughChoices = true;
        return;
      }
      this.saveModifications(datas, "/vote/" + this.vote._id);
    },
    saveModifications(voteData, url = "/") {
      this.createOrSaveRecord('votes', { ...voteData }).then(async () => {
          if (this.vote.nbChoices > 0) {
            await this.removeRecord('choices', { vote_id: this.vote._id }).catch(e => console.error(e));
          }
          for (const choice of this.choices) {
            if (choice.val) {
              try {
                await this.createOrSaveRecord('choices', {
                  vote_id: this.vote._id,
                  nbNote1: '0',
                  nbNote2: '0',
                  nbNote3: '0',
                  nbNote4: '0',
                  nbNote5: '0',
                  nbNote6: '0',
                  name: choice.val
                })
              } catch(err) {
                throw new Error("Problem handling something : " + err);
              }
            }
          }
          console.log(url);
          this.$router.push(url).catch(err => {
            throw new Error("Problem handling something : " + err);
          });
        })
        .catch(e => {
          console.error(e);
        });
    }
  }
};
</script>

<style lang="scss"></style>