var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('main',{staticClass:"my-auto"},[_c('div',{staticClass:"text-right bg-primary text-white d-flex"},[_c('font-awesome-icon',{attrs:{"icon":['fas', 'person-booth'],"size":"6x"}}),_c('p',{staticClass:"d-inline"},[_vm._v(" Nous avons l’habitude de voter avec le mode de scrutin uni-nominatif à 1 ou 2 tours : parmi plusieurs propositions, un votant donne 1 seule voix “pour”, la proposition avec le plus de voix l’emporte, le 2ème tour servant à dégager une majorité. Or cela fait depuis le 18eme siècle que l’on connaît les nombreuses faiblesses de ce mode de scrutin : les voix de protestation mélangées aux voix d’adhésion (ou pire : ignorées), le nombre de propositions divise les voix, le vote utile qui parasite nos choix objectifs, l’illusion de “majorité” du deuxième tour, la lecture extrêmement binaire de l’opinion des votants. A l’heure des algorithmes ultra-complexe, c’est difficilement défendable. Imaginez simplement que l’on puisse mettre 1 bulletin “Pour” et 1 bulletin “Contre”. Ou bien que l’on puisse mettre 2 ou 3 bulletins. Ou bien qu’on puisse voter comme au cinéma avec 1 avec des notes nuancées pour chaque proposition. Les élections auraient des résultats bien différents. ")])],1),_vm._m(0),_vm._m(1)])}
