  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: row;
}

.nmb_voices_bar > div {
  height: 100%;
}

.nmb_voices_bar > div:nth-child(1) {
  background-color: red;
}

.nmb_voices_bar > div:nth-child(2) {
  background-color: orange;
}

.nmb_voices_bar > div:nth-child(3) {
  background-color: yellow;
}

.nmb_voices_bar > div:nth-child(4) {
  background-color: greenyellow;
}

.nmb_voices_bar > div:nth-child(5) {
  background-color:  yellowgreen;
}

.nmb_voices_bar > div:nth-child(6) {
  background-color: green;
}

.repere {
  position: absolute;
  bottom: 15px;
  left: 50%;
  z-index: 20px;
  transform: translateX(-50%);
}

</style>
