import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import "./registerServiceWorker";
import "bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faBars, faTimes, faVoteYea, faPersonBooth, faBalanceScale, faBalanceScaleRight, faCaretUp  } from "@fortawesome/free-solid-svg-icons";
import { faTrashAlt, faSmile, faFrown } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

library.add(faBars);
library.add(faTimes);
library.add(faTrashAlt);
library.add(faSmile);
library.add(faFrown);
library.add(faVoteYea);
library.add(faPersonBooth);
library.add(faBalanceScale);
library.add(faBalanceScaleRight);
library.add(faCaretUp);

Vue.component("font-awesome-icon", FontAwesomeIcon);

Vue.config.productionTip = false;

new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
