<template>
  <div class="card bg-light mt-1" :class="customClass">
    <div class="card-body">
      <div class="mt-2 mb-4">
        <h4 class="card-title mb-3 text-primary">{{ title }}</h4>
        <p class="card-subtitle">{{ subTitle }}</p>
        <p class="text-danger small">{{ error }}</p>
      </div>
      <form @submit="submitCallback" method="post" autocomplete="on">
        <slot name="input"></slot>
        <div class="form-group mb-0 mt-4">
          <button type="submit" class="btn btn-primary btn" value="submit">{{ buttonSubmitText }}</button>
          <slot name="link"></slot>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "smallCardForm",
  props: {
    title: {
      type: String,
      required: true
    },
    subTitle: String,
    buttonSubmitText: {
      type: String,
      required: true
    },
    error: String,
    submitMethod: {
      type: Function,
      required: true
    },
    customClass: {
      type: String,
      required: false
    }
  },
  methods: {
    submitCallback(e) {
      e.preventDefault();
      this.submitMethod();
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  //margin: 0 16px;
