<template>
  <div class="col-10 mx-auto text-left mb-5">
    <h5 class="ml-3">{{ titleList }}</h5>
    <div v-if="list.length" class="d-flex flex-wrap">
        <div class="col-6" v-for="item in list" :key="item._id">
          <VotePreviewCard
            :title="item.title"
            :state="voteState(item)"
            :info="infoVote(item)"
            :link="linkVote(item)"
            :winningChoice="getWinningChoice(item._id)"
            :stateColor="colorStatus(item)"
          />
        </div>
      </div>
    <div v-else class="col-12">Pas de vote {{ singulier }} </div>
  </div>
</template>

<script>
import VotePreviewCard from "@/components/VotePreviewCard";

export default {
  name: "PreviewCardsList",
  components: {
    VotePreviewCard
  },
  props: {
    list: {
      type: Array,
      default: function() {
        return [];
      }
    },
    choices: {
      type: Array,
      default: function() {
        return []
      }
    },
    titleList: String,
    singulier: String
  },
  methods: {
    voteState: function(item) {
      if (item.status == 0) return "Non publié";
      else if (item.status == 1) return "Ouvert";
      else if (item.status == 2) return "Fermé";
      else if (item.status == 3) return "Archivé";
      return "";
    },
    infoVote: function(item) {
      if (item.status == 0) return "Éditer";
      else if (item.status == 1)
        return (
          item.nbBulletins + " bulletin" + (item.nbBulletins > 1 ? "s" : "")
        );
      return "";
    },
    linkVote: function(item) {
      return (item.status == 0 ? "/edit/" : "/vote/") + item._id;
    },
    getWinningChoice: function(vote_id) {
      const choice = this.choices.find(choice => choice.vote_id == vote_id);
      return choice ? choice.name : "";
    },
    colorStatus: function(item) {
      if (item.status == 0) return "text-primary";
      else if (item.status == 1) return "text-success";
      else if (item.status == 2) return "text-danger";
      else if (item.status == 3) return "text-warning";
      else return "text-primary";
    }
  }
};
</script>

<style lang="scss" scoped></style>
