<script>
export default {
    methods: {
        getMajorityMention(choice) {
            let maj_mention = 6;
            let total = 0;
            for(let i = 1; i <= maj_mention; i++) {
              total += (parseInt(choice['nbNote'+i]) || 0);
            }
            let maj_mention_count = (parseInt(choice['nbNote'+maj_mention]) || 0);
            while(maj_mention_count <= (total/2)) {
              maj_mention -= 1;
              maj_mention_count += parseInt(choice['nbNote'+maj_mention] || 0);
            }
            return maj_mention;
        },

        compareChoices(choice1, choice2, mm1, mm2, mgte, mlte) {
          const mention1 = mm1 || this.getMajorityMention(choice1);
          const mention2 = mm2 || this.getMajorityMention(choice2);

          if (mlte == 1 && mgte == 6) return 0;

          if (mention1 == mention2) {
            const mentionGreater = mgte || mention1;
            const nmb_gte1 = this.count_gte(choice1, mentionGreater);
            const nmb_gte2 = this.count_gte(choice2, mentionGreater);
            if (nmb_gte1 == nmb_gte2) {
              const mentionLower = mlte || mention1;
              const nmb_lte1 = this.count_lte(choice1, mentionLower);
              const nmb_lte2 = this.count_lte(choice2, mentionLower);
              if (nmb_lte1 == nmb_lte2) {
                const new_gte = mgte === 1 ? 1 : mentionGreater - 1;
                const new_lte = mlte === 6 ? 6 : mentionLower + 1;
                return this.compareChoices(choice1, choice2, mention1 - 1, mention2 - 1, new_gte, new_lte);
              }
              else
                return (nmb_lte1 < nmb_lte2 ? -1 : 1);
            } else
              return (nmb_gte1 > nmb_gte2 ? -1 : 1);
          } else
            return (mention1 > mention2 ? -1 : 1);
        },

        count_gte(choice, i_mention) {
          let nbVote = parseInt(choice['nbNote' + i_mention] || 0);
          const k_start = i_mention + 1; 
          for(let k = k_start; k <= 6; k++) {
            nbVote += parseInt(choice['nbNote' + k]) || 0;
          }
          return nbVote;
        },

        count_lte(choice, i_mention) {
          let nbVote = parseInt(choice['nbNote' + i_mention]) || 0;
          const k_start = i_mention + 1;
          for(let k = k_start; k > 0; k--) {
            nbVote += parseInt(choice['nbNote' + k]) || 0;
          }
          return nbVote;
        },

        sort_choices(choices) {
          return choices.sort((a, b) => {
            let result = this.compareChoices(a, b);
            return result;
          })
        }
    }
};
</script>